import { mapGetters, mapState } from "vuex";
import appPrice from "../../page-component/goods/app-price.vue";
import appGoodsTimer from "./app-goods-timer.vue";
import appDiyCompositionImage from '../app-diy-goods-list/app-diy-composition-image';

export default {
    name: "app-diy-goods-list",
    components: {
        'app-price': appPrice,
        'app-goods-timer': appGoodsTimer,
        appDiyCompositionImage
    },
    props: {
        list: {
            type: Array,
            default() {
                return [];
            }
        },
        goodsStyle: {
            type: Number,
            default() {
                return 1;
            }
        },
        textStyle: {
            type: Number,
            default() {
                return 1;
            }
        },
        listStyle: {
            type: Number,
            default() {
                return -1;
            }
        },
        showBuyBtn: {
            type: Boolean,
            default() {
                return false;
            }
        },
        buyBtnText: {
            type: String,
            default() {
                return '';
            }
        },
        buyBtnStyle: {
            type: Number,
            default() {
                return 1;
            }
        },
        fill: {
            type: Number,
            default() {
                return 1;
            }
        },
        showGoodsName: {
            type: Boolean,
            default() {
                return true;
            }
        },
        showGoodsPrice: {
            type: Boolean,
            default() {
                return true;
            }
        },
        showGoodsTag: {
            type: Boolean,
            default() {
                return true;
            }
        },
        goodsCoverProportion: {
            type: String,
            default() {
                return '1-1';
            }
        },
        customizeGoodsTag: {
            type: Boolean,
            default() {
                return false;
            }
        },
        goodsTagPicUrl: String,
        sign: String,
        buttonColor: {
            type: String,
            default() {
                return '';
            }
        },
        //是否显示划线价
        isUnderLinePrice: {
            type: Boolean,
            default() {
                return true;
            }
        },
        pageHide: Boolean,
        theme: Object,
        showProgressBar: {
            type: Boolean,
            default() {
                return false;
            }
        }
    },
    data() {
        return {
            imgRadius: '16rpx 16rpx 0 0',
            lisRadius: '16rpx',
			disableColor: '#999999'
        };
    },
    computed: {
        ...mapState({
            appImg: state => state.mallConfig.__wxapp_img.mall,
            appSetting: state => state.mallConfig.mall.setting,
            platform: function(state) {
                return state.gConfig.systemInfo.platform;
            }
        }),
        ...mapGetters('mallConfig',{
            vip: 'getVip',
            getVideo: 'getVideo'
        }),
        newData() {
            return this.list;
        },
        listClass() {
            switch (this.listStyle) {
                case 2:
                    return `dir-left-wrap column-2 main-between `;
                default:
                    return ``;
            }
        },
        buyBtnClass() {
            let buyBtnClass = ``;
            if (this.buyBtnStyle === 2 || this.buyBtnStyle === 4) {
                buyBtnClass += `buy-btn-border `;
            }
            if (this.buyBtnStyle === 4 || this.buyBtnStyle === 3) {
                buyBtnClass += `buy-btn-radius`;
            }
            return buyBtnClass;
        },
        btnStyle() {
            let btnStyle = ``;
            if (this.buyBtnStyle === 1 || this.buyBtnStyle === 3) {
                btnStyle += `background-color: ${this.buttonColor};color: #ffffff;`;
            } else {
                btnStyle += `border-color: ${this.buttonColor};color: ${this.buttonColor};`;
            }
            return btnStyle;
        },
        disableBtnStyle() {
            let btnStyle = ``;
            if (this.buyBtnStyle === 1 || this.buyBtnStyle === 3) {
                btnStyle += `background-color: ${this.disableColor};color: #ffffff;`;
            } else {
                btnStyle += `border-color: ${this.disableColor};color: ${this.disableColor};`;
            }
            return btnStyle;
        },
        coverPicHeight() {
            if (this.goodsCoverProportion === '1-1') {
                return `702rpx`;
            } else {
                return `468rpx`;
            }
        },
        goodsClass() {
            let goodsClass = ``;
            if (this.goodsStyle === 2) {
                goodsClass += `border`;
            }
            if (this.textStyle === 2) {
                goodsClass += `text-center`;
            }
            return goodsClass;
        },
        tag() {
            let tag = '';
            switch (this.sign) {
                case 'miaosha':
                    tag = '秒杀';
                    break;
                case 'bargain':
                    tag = '砍价';
                    break;
                case 'pintuan':
                    tag = '人团';
                    break;
                case 'advance':
                    tag = '预售';
                    break;
                case 'pick':
                    tag = 'N元任选';
                    break;
                case 'composition':
                    tag = '套餐组合';
                    break;
                case 'gift':
                    tag = '社交送礼';
                    break;
                case 'flash-sale':
                    tag = '限时抢购';
                    break;
                case 'wholesale':
                    tag = '商品批发';
                    break;
                case 'exchange':
                    tag = '礼品卡';
                    break;
                case 'step':
                    tag = '步数宝';
                    break;
                default:
                    tag = null;
                    break;
            }
            return tag;
        },
        showTimer() {
            if (!(this.sign === 'miaosha' || this.sign === 'bargain' || this.sign === 'lottery' || this.sign === 'advance' || this.sign === 'flash-sale')) return false;
            return true;
        },
    },
    methods: {
        jump(data) {

            if (data.video_url && this.getVideo == 1 && data.sign !== 'lottery' && data.sign !== 'bargain' && data.sign !== 'wholesale') {
                let id = data.id;
                if (data.sign === 'advance') {
                    id = data.goods_id;
                }
                if (data.sign === 'gift') {
                    id = id + '&is_share=1';
                }


                uni.navigateTo({
                    url: `/pages/goods/video?goods_id=${id}&sign=${data.sign}`
                });






            } else {
                uni.navigateTo({
                    url: data.page_url
                });
            }






        },
        isShowOriginalPrice(goods) {
            return this.isUnderLinePrice && goods.original_price && this.showGoodsPrice && goods.is_negotiable !== 1;
        },
    }
}
